                    <input type="text" maxlength="50" v-on:keydown="ListFiltering" v-on:keyup="ListFiltering" v-on:mouseup="SelectAllSearch" v-on:mouseleave="SelectAllSearch" id="searchinput"
                        v-model="MainSearchmodel" placeholder="Search by Name"
                        class="form-control searchcustomfilter border-right-0" autocomplete="off">
                    <div class="input-group-append">
                        <span class="input-group-text bg-transparent bg-white border-left-0">
                            <i class="fa fa-search"></i>
                        </span>
                    </div>
                </div>
            </div>
            <ul class="nav nav-pills flex-column left-tab responivecustomtab lefttabiconfix" >
                <li v-for="list in searchColumnfilter.length == 0 ? leftTabList : searchColumnfilter" :class="{'nav-link left-menu': true,  'active': list.goTo === $route.path }">
                    <router-link :to="list.goTo" style="align-items: center;">
                        <div class="listing"><i :class="list.icon" class="mr-2"></i>{{ list.title }}</div>
                    </router-link>
                </li>
            </ul>
        </div>
