                },
                {
                    title: 'Break',
                    icon: 'fa fa-clock-o',
                    goTo: '/Break/Index',
                },
                // {
                //     title: 'Canned Reply',
                //     icon: 'fa fa-envelope',
                //     goTo: '/CannedReply/Index',
