                    icon: 'fa fa-address-card-o',
                    goTo: '/UserTitle/Index',
                },
            ]
        };
    },
  methods: {
    SelectAllSearch(){
        var vm = this;
        vm.GetSelectedValue = window.getSelection().toString()
    },
    ListFiltering(){
        var vm = this;
        var input = document.getElementById('searchinput');

        var filterinput = input.value;
        if(vm.GetSelectedValue != ''){
            filterinput = filterinput.replaceAll(vm.GetSelectedValue,'')
            vm.GetSelectedValue = '';
        }
        var searchcol = [];
        var searchfilter = [];
        vm.leftTabList.forEach((indexitem) => {
            searchfilter.push(indexitem);
            searchcol = searchfilter.filter(searchitem => {
                var displayMainColname = vm.$t(searchitem.title);
                return displayMainColname.replace(/\W/g, "").toLowerCase().includes(filterinput.replace(/\W/g, "").toLowerCase());
            })
        })
        vm.searchColumnfilter = searchcol;
        searchfilter = [];
        searchcol = [];
    }
  },
};
